@import url(https://fonts.googleapis.com/css2?family=Work+Sans:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap);
.header {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.title {
  font-weight: 700;
  font-size: 32px;
  color: #404040;
}

.subtitle {
  color: #8c8c8c;
  padding: 10px;
  font-weight: 500;
}

.searchBar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.searchBar-container img {
  position: relative;
}

.searchBar {
  margin-top: 30px;

  border-radius: 40px;
  padding: 20px 0px;
  padding-left: 80px;
  padding-right: 30px;
  width: 60vw;

  border: 1px solid #fdecec;
  background: url(data:image/png;base64,) no-repeat scroll 24px 18px;
  background-color: #fdecec;
}

.searchBar::-webkit-input-placeholder {
  color: #ff1510;
}

.searchBar:-ms-input-placeholder {
  color: #ff1510;
}

.searchBar::-ms-input-placeholder {
  color: #ff1510;
}

.searchBar::placeholder {
  color: #ff1510;
}

.toggle-icon {
  max-width: 50px;
  margin-bottom: -8px;
  margin-left: 8px;
  margin-right: 8px;
}

.favorite-icon {
  max-width: 15px;
  margin-bottom: -2px;
}

.favorite-outlined-icon {
  max-width: 20px;
  max-height: 19px;
  margin-bottom: -2px;
}

.heroesHeader-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}

.heroes-header-total {
  color: #b9b9b9;
  font-weight: 500;
}

.heroes-header-text-color {
  color: #ff1410a2;
}

.heroes-noun-superhero-icon {
  max-width: 14px;
  margin-bottom: -4px;
  margin-right: 4px;
}

.heroes-header-favorite > img {
  margin-left: 8px;
  margin-right: 8px;
}

.hero-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-top: 25px;
}

.hero-list > div {
  max-width: calc(25% - 20px);
  padding: 10px;
}

.hero-list span:nth-last-child() {
  text-align: end;
}

.hero-thumbnail {
  width: 100%;
  height: 225px;
  object-fit: cover;
}

.hero-description {
  display: flex;
  justify-content: space-between;
}

.heroes-container {
  padding: 50px 100px;
  display: flex;
  flex-direction: column;
}

* {
  font-family: 'Work Sans', sans-serif;
}

body {
  margin: 0;
}

